:root {
  --background-color: white;
  --section-background-color: #181818;
  --font-color: #2b2b2b;
  --selection-color: rgba(46, 141, 204, 0.3);
  --selection-text-color: #000;
}

::-moz-selection {
  background-color: rgba(46, 141, 204, 0.3);
  background-color: var(--selection-color);
  color: #000;
  color: var(--selection-text-color);
}

::selection {
  background-color: rgba(46, 141, 204, 0.3);
  background-color: var(--selection-color);
  color: #000;
  color: var(--selection-text-color);
}

::-moz-selection {
  background-color: rgba(46, 141, 204, 0.3);
  background-color: var(--selection-color);
  color: #000;
  color: var(--selection-text-color);
}

html {
  height: 100%;
  box-sizing: border-box;
  font-family: sans-serif;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: transparent;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

a {-webkit-tap-highlight-color: rgba(0,0,0,0)}

html, body {
  background-color: white;
  background-color: var(--background-color);
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;      
  text-rendering: optimizeLegibility;
  background-color: white;
  background-color: var(--background-color);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 20vmin;
}

.App-header {
  background-color: var(--section-background-color);
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-section {
  background-color: var(--section-background-color);
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}




.image {
  box-shadow: 0px 0px 20px 3px rgba(0, 0, 0, 0.3);
}

.text {
  padding: 16px 8px;
  font-size: 14px;
  font-weight: 500;
  background-color: #f5f5f5;
  border: 1px solid #00000012;
}
.Api-masonry {
  display: layout(masonry);
  --padding: 20;
  --columns: auto;
}

.Api-masonry .block {
  font-weight: 500;
  border: 1px solid #00000012;
  font-size: calc(10px + 2vmin);
  overflow: hidden;
}

.Api-masonry .block img {
  height: 100%;
  width: 100%;
}

.Api-masonry .block .inner {
  padding: 16px 8px;
}

.Api-masonry .block .inner h1 {
  margin: 8px 0 8px;
  font-size: 1.4rem;
  font-weight: 600;
  line-height: 1.6rem;
}

.Api-masonry .block .inner span {
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.8rem;
}

.Api-masonry .block .inner p {
  font-size: 1rem;
  font-weight: 300;
  color: #4e4e4e;
}
.Message {
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #444;
  padding: 36px;
  width: 70%;
  margin: auto;
}

.Message h1 {
  margin: 0px;
  padding: 0px;
  font-size: calc(10px + 4vmin);
  padding-bottom: 36px;
}

.Message-url-box {
  background-color: #f5f5f5;
  border: 1px solid #00000012;
  border-radius: 3px;
  font-size: 1em;
  padding: 12px 36px;
  width: 100%;
  margin-bottom: 36px;
}

.Message-image {
  width: 100%;
  box-shadow: 0px 0px 20px 3px rgba(0, 0, 0, 0.13);
}
